import React from "react";
import { Card, Col, Container, Row } from "react-bootstrap";

import { Bestiary } from "./Cards/Bestiary"

const Divider = ({ color }) => (
    <hr
        style={{
            color: color,
            backgroundColor: color,
            height: 5
        }}
    />
);

const Creature_Card = ({
        //Basic    
        creatureName, challengeRating, exp, alignment, 
        creatureSize, creatureType, initative, senses, perception, 
        //Defenses
        armorClasses,
        hd, saves, defensiveAbilities,
        //Offenses
        speeds, meleeAttacks, rangedAttacks, specialAttacks,
        spells, space, reach,
        //Stats
        stats, bab, cmd,
        feats, skills, racialMods, languages,
        //Abilities
        specialQualities, specialAbilities,
        //Lore
        environment, organization, treasure, desc
    }) => {
    if(!creatureName) return <div />

    let hp=0;

    return (
        <Card className="creature-card">
            <Card.Title>{creatureName}</Card.Title>
            <Container>
                <Row>
                    <Col><b>CR</b>: {challengeRating}</Col>
                    <Col><b>XP</b>: {exp}</Col>
                </Row>
                <Row>{alignment} {creatureSize} {creatureType}</Row>
                <Row>
                    <Col>{initative}</Col>
                    <Col><b>Senses</b>: {senses.join(", ")}</Col>
                    <Col><b>Perception</b>: +{perception}</Col>
                </Row>
            </Container>

            <hr></hr>
            <Container className="defenses"> 
                <Card.Subtitle>Defenses</Card.Subtitle>
                <Row style={{width:'50%'}}>
                    <Col><b>AC</b>: {armorClasses[0]}</Col>
                    <Col><b>Touch</b>: {armorClasses[1]}</Col>
                    <Col><b>Flat-footed</b>: {armorClasses[2]}</Col>
                    <Col>({armorClasses[4]})</Col>
                </Row>
                <Row>
                    <Col><b>HP</b>: {hp} ({hd})</Col>
                </Row>
                <Row>
                    <Col><b>Fort</b>: {saves[0]}</Col>
                    <Col><b>Ref</b>: {saves[1]}</Col>
                    <Col><b>Will</b>: {saves[2]}</Col>
                </Row>
                <Row>
                    <Col><b>Defensive Abilities</b>: {defensiveAbilities.join(", ")}</Col>
                </Row>
            </Container>

            <hr></hr>
            <Container className="offenses">
                <Card.Subtitle>Offenses</Card.Subtitle>
                <Row>
                    { speeds.map((data, key) => {
                        return(
                            <Col><b>{data[0]}</b>: {data[1]}ft.</Col>
                        );
                    })}
                </Row>
                <Row>
                    <Col><b>Space</b>: {space}</Col>
                    <Col><b>Reach</b>: {reach}</Col>
                </Row>
            </Container>
        </Card>
    );
}

export const Creature_Cards = () => {
    return (
        <div>
            { Bestiary.map((data, key) => {
                return (
                    <div className="creature-card">
                        <Creature_Card 
                        creatureName={data.creatureName}
                        challengeRating={data.challengeRating}
                        exp={data.exp}
                        alignment={data.alignment}
                        creatureSize={data.creatureSize}
                        creatureType={data.creatureType}
                        initative={data.initative}
                        senses={data.senses}
                        perception={data.perception}
                        //Defenses
                        armorClasses={data.armorClasses}
                        hd={data.hd}
                        saves={data.saves}
                        defensiveAbilities={data.defensiveAbilities}
                        //Offenses
                        speeds={data.speeds}
                        meleeAttacks={data.meleeAttacks}
                        rangedAttacks={data.rangedAttacks}
                        specialAttacks={data.specialAttacks}
                        spells={data.spells}
                        space={data.space}
                        reach={data.reach}
                        />
                    </div>
                );
            })}
        </div>
    );
}